@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css);
.header {
    height: 100vh;
    text-align: center;
    min-height: 500px;
}

.bio {
    padding: 0 7vw;
}

.experience {
    padding: 40px 7vw 40px 7vw;
    margin-bottom: 200px;
}

.tab-body {
    background-color: #232931;
    min-height: 500px;
    padding: 20px 5vw 100px;
}

.footer {
    height: 125px;
    padding: 40px 25px;
    text-align: center;
    background-color: #303841;
}

.navbar {
    background-color: #303841;
}

.profile {
    padding-top: 20vh;
    padding-bottom: 20vh;
}

.icon-button {
    color: #FFFFFF;
}

.icon-button:hover {
    color: #4f5661;
}

.plus-background {
    background-image: url(data:image/png;base64,);
    background-repeat: repeat;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

.material-icon {
    height: 40px;
    width: 40px;
}

.title {
    margin-bottom: 40px;
    font-size: 42pt;
    font-weight: bold;
    -webkit-text-decoration: underline #f6c90e;
            text-decoration: underline #f6c90e;
    text-align: center;
}

.subtitle {
    margin-bottom: 15px;
    font-size: 28pt;
}

.position-text {
    margin-bottom: 15px;
    font-size: 18pt;
    font-weight: bold;
}

.basic-card {
    color: white;
    background-color: #333b45;
    flex: 1 0 auto;
    text-align: left;
}

.experience-card {
    color: white;
    background-color: #333b45;
    min-height: 400px;
    text-align: left;
}

.project-card {
    color: white;
    background-color: #333b45;
    min-height: 400px;
    text-align: left;
}

.project-card-action {
    background-color: #333b45;
    height: 100px;
    color: white;
}

.project-card-header {
    background-color: #333b45;
    color: white;
    text-align: left;
}

.card-text {
    padding: 10px;
    font-size: 14pt;
}


.experience-card-img {
    background-color: #2c343e;
    height: 300px;
    width: 200px;
}


html {
    color: white;
}

body {
    margin: 0;
    padding: 0;
    font-size: 16pt;
    color: white;
    background-color: #303841;
}

h1 {
    margin-bottom: 20px;
}

h2 {
    margin: 0;
    font-size: 24px;
    font-weight: normal;
}

p {
    margin-bottom: 40px;
}

li {
    margin-top: 10px;
    margin-bottom: 10px;
}

a {
    color: #f6c90e;
}

a:hover {
    color: #9b820c;
}

